import Vue from "vue";
import Router from "vue-router";
import jToken from "../common/jToken"
import {EventBus, Events} from "../common/eventBus";

Vue.use(Router);

const router = new Router({
    mode: 'history',

    routes: [
        {
            path: "/news",
            name: 'news',
            component: () => import(/* webpackChunkName: "home" */ "@/views/News")
        },
        {
            path: "/",
            name: 'categoryChoose',
            component: () => import(/* webpackChunkName: "catalog" */ "@/views/CategoryChoose")
        },
        {
            path: "/catalog/:categoryId",
            name: 'catalog',
            component: () => import(/* webpackChunkName: "catalog" */ "@/views/Catalog")
        },
        {
            path: "/catalog/:categoryId/:subCategoryId",
            name: 'catalogWithSub',
            component: () => import(/* webpackChunkName: "catalog" */ "@/views/Catalog")
        },
        {
            path: "/search/:request",
            name: 'search',
            component: () => import(/* webpackChunkName: "catalog" */ "@/views/Catalog")
        },
        {
            path: "/waybills",
            name: "waybills",
            component: () => import(/* webpackChunkName: "waybills" */ "@/views/Waybills")
        },
        {
            path: "/waybill/:id",
            name: "waybill",
            component: () => import(/* webpackChunkName: "waybill" */ "@/views/Waybill")
        },
        {
            path: "/ordering",
            name: "ordering",
            component: () => import(/* webpackChunkName: "ordering" */ "@/views/Ordering")
        },
        {
            path: "/login",
            name: "login",
            component: () => import(/* webpackChunkName: "login" */ "@/views/Login")
        },
        {
            path: "/register",
            name: "register",
            component: () => import(/* webpackChunkName: "register" */ "@/views/Register")
        }
    ],
});


router.beforeEach((to, from, next) => {
    EventBus.$emit(Events.routerLoadingStart);

    if (!jToken.has() && to.name !== "login" && to.name !== "register") {
        next({
            name: 'login',
        })
    } else {
        next();
    }

    next();
});

router.afterEach((to, from) => {
    EventBus.$emit(Events.routerLoadingFinish);
});

export default router;